.contact-info-section {
  min-height: 600px;
  background: linear-gradient(rgba(255, 255, 255, 0.8), 37%, #0a770e61);
  width: 100%;
  display: grid;
  grid-template-columns: 1fr;
  padding: 4%;
}

.contact-info-section .inner-content {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 3fr;
}
.contact-info-section h2 {
  grid-row: 1;
  grid-column: 1 / span 5;
  margin: 0 auto;
  padding: 4%;
  padding-top: 2%;
  font-size: 3.5em;
}
.contact-info-section .contact-info {
  grid-column: 1 / span 5;
  grid-row: 2;
  margin: 0 auto;
  text-align: center;
  font-size: 1.4em;
}

.contact-info-section .contact-slider {
  grid-column: 1 / span 5;
}

.contact-info-section .slick-track {
  overflow: hidden;
}
.contact-info-section .contact-slider .slick-slide {
  margin-right: 10px;
}
.contact-info-section .contact-slider-img {
  height: 200px;
  width: 200px;
  object-fit: cover;
}

@media (max-width: 450px) {
  .contact-info-section .inner-content {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr 3fr;
    max-width: 100%;
    overflow: hidden;
  }

  .contact-info-section h2 {
    grid-column: 1;
  }
  .contact-info-section .contact-info {
    margin-top: -5%;
  }
}
